import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//  vue-ls
import Storage from 'vue-ls'
// 
import MyPlugin from './myPlugin/index';
//  vant 
import { Lazyload, RadioGroup, Radio } from 'vant';
// import getVant from './plugins/index.js'

// "vue-ls" Local Storage
// "vue-ls"  localStorage API"vue-ls" 
// vue-ls 
const options = {
  // key 
  namespace: 'vue-ls_',
  //  Vue.  this.$ this.$ls
  name: 'ls',
  // localsessionmemory
  storage: 'local'
}


//  Vue 
const app = createApp(App)



//  Toast 
app.use(Lazyload, {
  lazyComponent: true,
});
app.use(Radio).use(RadioGroup);
// getVant(App)

// .use(MyPlugin) 
app.use(store).use(router).use(Storage, options).use(MyPlugin).mount('#app')
