import { mapState } from "vuex";
import TopNav from '@/components/home/TopNav.vue';
import Menu from '@/components/home/Menu.vue';
import LoginBox from '@/components/home/LoginBox.vue';
import PlayBox from '@/components/home/PlayBox.vue';
import SongDetail from '@/views/SongDetail.vue';
export default {
  name: 'App',

  data() {
    return {
      // (LoginBox)
      isLogin: 0,
      //  LoginBox 
      isLoginBox: 0
    };
  },

  mounted() {
    // 
    this.isLogin = sessionStorage.getItem('user') != null ? sessionStorage.getItem('user') : false;
  },

  computed: { ...mapState(['isPlayBox'])
  },
  components: {
    TopNav,
    Menu,
    LoginBox,
    PlayBox,
    SongDetail
  },
  methods: {
    showDetail() {
      //  ref
      this.$refs.songDetail.goUp();
    },

    hideDetail() {
      this.$refs.songDetail.goDown();
    }

  }
};