//  axios 
import service from "..";

// 
export function getSongDetail(id) {
  return service({
    method: 'GET',
    url: `/song/detail?ids=${id}`
  })
}

//  url()
export function getSongUrl(id) {
  return service({
    method: 'GET',
    url: `/song/url?id=${id}`
  })
}

// 
export function getMusicLyrics(id) {
  return service({
    method: 'GET',
    url: `/lyric?id=${id}`
  })
}

// ()
export function getMusicComment(id, limit, cookie) {
  return service({
    method: 'GET',
    url: `/comment/music?id=${id}&limit=${limit}&cookie=${cookie}`
  })
}

// (POST )POST  url  url  
export function postComment(t, type, id, content, timestamp, cookie) {
  return service({
    method: 'POST',
    url: `/comment?t=${t}&type=${type}&id=${id}&content=${content}&timestamp=${timestamp}&cookie=${cookie}`
  })
}