import SongDetailTopNav from '@/components/songDetail/SongDetailTopNav.vue';
import Lyrics from '@/components/songDetail/Lyrics.vue';
import Comments from '@/components/songDetail/Comments.vue';
import PostComment from '@/components/songDetail/PostComment.vue';
export default {
  data() {
    return {
      // 
      isFirstShow: 0,
      // 
      isTopShowSongName: 0
    };
  },

  created() {
    // 
    this.listenerFunction();
  },

  beforeDestroy() {
    // 
    document.removeEventListener("scroll", this.listenerFunction);
  },

  methods: {
    // ( vant  <van-popup></van-popup>)
    goUp() {
      this.$refs.songDetail.classList = 'songDetail up'; // () ()

      this.isFirstShow = 1;
    },

    // 
    goDown() {
      this.$refs.songDetail.classList = 'songDetail down';
    },

    //  Comments  Lyrics  SongDetailTopNav  goDown
    goDown1() {
      this.$refs.SongDetailTopNav.goDown();
    },

    //  handleScroll()
    listenerFunction(e) {
      document.addEventListener('scroll', this.handleScroll, true);
    },

    // 
    handleScroll() {
      //  210 px 
      this.isTopShowSongName = this.$refs.container.scrollTop >= 210 ? 1 : 0;
    }

  },
  components: {
    SongDetailTopNav,
    Lyrics,
    Comments,
    PostComment
  }
};