// 
import { mapMutations, mapState } from 'vuex';
import { getLoginKey, getLoginCreate, getLoginCheck, getStatus, getCaptcha, cellphonePasswordLogin, cellphoneCaptchaLogin, refreshLogin } from '@/request/api/loginBox.js';
export default {
  name: 'LoginBox',

  data() {
    return {
      // 
      closeNum: 0,
      // 
      x: 600,
      y: 150,
      // 
      leftOffset: 0,
      topOffset: 0,
      // 
      isMove: false,
      //  body 
      body: 1,
      // 
      verification: 'password',
      // key
      unikey: '',
      qrurl: '',
      //   base64 
      qrimgs: '',
      // 
      qrCheckData: {},
      // 
      isLoging: false,
      // 
      message: '',
      // 
      phone: '',
      // 
      password: '',
      // 
      captcha: '',
      // 
      user: {
        // id
        userid: 0,
        // 
        nickname: '',
        // 
        avatarUrl: '',
        // 
        follows: 0,
        // 
        followeds: 0,
        // 
        locationStatus: 0,
        // 
        signature: '',
        // 
        level: 0,
        // 
        listenSongs: 0,
        // 
        gender: 1,
        // 
        newUser: false,
        // 
        province: 0,
        // 
        city: 0,
        // ()
        birthday: 0,
        // 
        createTime: 0,
        // 
        playlistCount: 0,
        // 
        vipType: 0
      },
      //  cookie ()
      cookie: '',
      //  getStatus()
      getStatusOk: 0
    };
  },

  async mounted() {
    // ()
    let timestamp = new Date().getTime(); //  key

    let res = await getLoginKey(timestamp);
    this.unikey = res.data.data.unikey; // console.log('unikey:', this.unikey);
    //  key 

    let res1 = await getLoginCreate(this.unikey, timestamp); // console.log(res1);

    this.qrurl = res1.data.data.qrurl;
    this.qrimgs = res1.data.data.qrimg; // 
    // Request failed with status code 404 

    this.getCheck(); //  1s 
    // setTimeout(async () => {
    //   this.getCheck()
    // }, 1000);
  },

  // 
  computed: {
    //top left px
    position() {
      return `top:${this.y}px;left:${this.x}px;`;
    }

  },
  methods: {
    //  CSDNhttps://blog.csdn.net/ZuoZuoDangerou/article/details/118191137
    //
    mousedown(event) {
      this.leftOffset = event.offsetX;
      this.topOffset = event.offsetY;
      this.isMove = true;
    },

    //
    mousemove(event) {
      // 
      if (!this.isMove) return;
      this.x = event.clientX - this.leftOffset;
      this.y = event.clientY - this.topOffset;
    },

    // move
    mouseup() {
      this.isMove = false;
    },

    // ()
    close() {
      this.updateIsLoginBox(false);
      this.closeNum++; // 

      this.isLoging = false;
    },

    // 
    codeMouseover() {
      this.$refs.code_box.classList.add('smaller');
      this.$refs.pic.classList.add('picMove');
    },

    codeMouseleave() {
      this.$refs.code_box.classList.remove('smaller');
      this.$refs.pic.classList.remove('picMove');
    },

    // (803  cookie)
    async getCheck() {
      let timestamp = new Date().getTime(); // 

      let res = await getLoginCheck(this.unikey, timestamp); // console.log('getLoginCheck', res);
      // ('' '' '' '')

      this.message = res.data.message; // 

      this.qrCheckData = res.data; //  cookie( cookie)

      sessionStorage.setItem('qrCookie', res.data.cookie); // 

      this.isLoging = true; // 803  cookie

      if (res.data.code == 803) sessionStorage.setItem('cookie', res.data.cookie);
    },

    // ()
    async loginRefresh() {
      let timestamp = new Date().getTime();
      const res = await refreshLogin(this.unikey, timestamp); // console.log(res);
    },

    // 
    async getStatus() {
      const res = await getStatus(sessionStorage.getItem("qrCookie")); // console.log('', res);
      // 

      sessionStorage.setItem('isLoging', res.data.data.account.status); // 0
      //  id

      this.user.userid = res.data.data.account.id; // 

      this.user.avatarUrl = res.data.data.profile.avatarUrl; // 

      this.user.nickname = res.data.data.profile.nickname; // 

      this.user.locationStatus = res.data.data.profile.locationStatus; // ( JSON )

      sessionStorage.setItem('userInfo', JSON.stringify(this.user));
    },

    //  
    async cellphonePasswordLogin() {
      let res = await cellphonePasswordLogin(this.phone, this.password); // console.log(res);
    },

    // 
    async getCaptcha() {
      let res = await getCaptcha(this.phone); // console.log(res);
    },

    //  
    async cellphoneCaptchaLogin() {
      let res = await cellphoneCaptchaLogin(this.phone, this.captcha); // console.log(res);
    },

    //  methods  store  index.js  mutations 
    ...mapMutations(['updateIsLoginBox'])
  },
  watch: {
    //   closeNum
    closeNum() {
      // 
      this.x = 600;
      this.y = 150;
      this.body = 1;
    },

    //  isLoging 
    isLoging() {
      // 
      if (sessionStorage.getItem('user') == true) retrurn; //  3s 

      if (sessionStorage.getItem('user') == null) {
        let timerIsLogin = setInterval(async () => {
          // 
          this.getCheck(); // 

          let code = this.qrCheckData.code; //  code800, 801, 802, 803( cookies)

          if (code == 800) {
            //
            this.$refs.mask.style.display = 'block';
            console.log('800', this.message);
          } // 100(5) '801 '  '800 '
          else if (code === 801 && this.isLoging == true) console.log('801', this.message);else if (code === 802) console.log('802', this.message);else if (code === 803) {
            console.log('803', this.message); //  uid

            this.getStatus(); //

            sessionStorage.setItem("user", true); // 

            clearInterval(timerIsLogin); //  LoginBox.vue

            this.close(); //  1s 

            setTimeout(() => {
              this.$router.go(0);
            }, 1000);
          } // 
          else if (this.isLoging == false) {
            clearInterval(timerIsLogin); // console.log('!');
          }
        }, 3000);
      }
    }

  }
};