// 
import { mapMutations, mapState } from 'vuex';
import bus from '@/components/home/eventBus';
export default {
  data() {
    return {
      // 
      isPlay: 0,
      // 
      isVip: 0,
      // 
      isTranslate: 1,
      // 
      isTransliteration: 0,
      // 
      isFunctionBar: 0,
      // 
      nowLrc: ''
    };
  },

  methods: {
    // 
    goDetail() {
      //  SongDetailTopNav.vue  goDown
      this.$emit('goDown1'); // this.songListId == 0  banner == 1 ''== 2''

      if (this.songListId == 0) {
        this.$router.push('/DiscoverMusic/DiscoverRecommend');
      } else if (this.songListId == 1) {
        this.$router.push('/RecommendSongs');
      } else if (this.songListId == 2) {
        this.$router.push('/Search/SearchSingle');
      } else {
        // 
        this.$router.push({
          path: '/SongListDetail',
          query: {
            id: this.songListId,
            // 1 2
            type: 1
          }
        });
      }
    },

    // 
    goSingerPage(id) {
      // 
      this.$emit('goDown1');
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 
    goAlbumDetail(id) {
      // 
      this.$emit('goDown1');
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1 2
          type: 2
        }
      });
    },

    //  methods  store  index.js  mutations 
    ...mapMutations(['updateNowLrc'])
  },

  //  created 
  created() {
    //  PlayBox.vue  isPlay
    bus.on('isPlay', val => {
      this.isPlay = val;
    });
  },

  // 
  computed: { ...mapState(['playList', 'playListIndex', 'songListName', 'songListId', 'lyricList', 'currentTime']),

    // 
    lyric() {
      let arr;

      if (this.lyricList.lyric) {
        // 
        arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
          //  item 
          let min = item.slice(1, 3);
          let sec = item.slice(4, 6);
          let mill = item.slice(7, 10);
          let lrc = item.slice(11, item.length); // 

          let time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill); // 

          if (isNaN(Number(mill))) {
            mill = item.slice(7, 9);
            lrc = item.slice(10, item.length);
            time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          } // console.log(min, sec, mill, lrc);
          // 


          return {
            min,
            sec,
            mill,
            lrc,
            time
          };
        });
        arr.forEach((item, i) => {
          if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
            //  100s ?
            item.pre = 100000;
          } else {
            // 
            item.pre = arr[i + 1].time;
          }
        });
      } // console.log(arr);


      return arr;
    }

  },
  watch: {
    currentTime() {
      //  DOM:
      // let p = document.querySelector("p.active");
      // console.log([p]);
      //  active 
      let temP = this.$refs.pLrc.filter(item => item.className == 'active'); // console.log(temP[0]);
      // 

      if (temP[0] && temP[0].offsetTop > 150) this.$refs.lyric_bottom_left.scrollTop = temP[0].offsetTop - 150; // 

      for (let i = 0; i < this.lyric.length; i++) {
        const currentTimeSec = this.currentTime * 1000;

        if (currentTimeSec >= this.lyric[i].time && currentTimeSec < this.lyric[i].pre && this.nowLrc !== this.lyric[i].lrc) {
          // 
          this.nowLrc = this.lyric[i].lrc; // console.log(this.nowLrc);

          this.updateNowLrc(this.nowLrc);
        }
      }
    }

  }
};