//  axios 
import service from "..";

// 
export function getUserDetail(uid, cookie) {
  return service({
    method: 'GET',
    url: `/user/detail?uid=${uid}&cookie=${cookie}`
  })
}

//  , mv, dj  
export function getUserSubcount(uid) {
  return service({
    method: 'GET',
    url: `/user/subcount?uid=${uid}`
  })
}

//  VIP ()
export function getVipInfo(uid) {
  return service({
    method: 'GET',
    url: `/vip/info?uid=${uid}`
  })
}

// ()()
export function getUserPlaylist(uid) {
  return service({
    method: 'GET',
    url: `/user/playlist?uid=${uid}&limit=200`
  })
}

// /
export function getUserAudio(uid) {
  return service({
    method: 'GET',
    url: `/user/audio?uid=${uid}`
  })
}

// ()
export function getSongListSongs(id, cookie) {
  return service({
    method: 'GET',
    url: `/playlist/track/all?id=${id}&cookie=${cookie}`
  })
}