// 
import { mapMutations, mapState } from 'vuex';
import { getUserPlaylist } from '@/request/api/personalHome';
export default {
  data() {
    return {
      choseColor: 'rgb(240, 240, 240)',
      // 
      menuItems: [{
        id: 1,
        title: '',
        rootPath: '/DiscoverMusic'
      }, {
        id: 2,
        title: '',
        rootPath: '/Podcast'
      }, {
        id: 3,
        title: '',
        rootPath: '/Video'
      }, {
        id: 4,
        title: '',
        rootPath: '/Focus'
      }, {
        id: 5,
        title: '',
        rootPath: '/Live'
      }, {
        id: 6,
        title: 'FM',
        rootPath: '/Fm'
      }],
      // 
      isCreateSongList: 0,
      // 
      isCollectSongList: 0,
      // 
      user: {},
      // 
      playlist: [],
      // 
      userCreateLists: [],
      // 
      userCollectLists: [],
      //  id
      userLoveListId: 0,
      // 
      mineLoveAcive: 0,
      // 
      isLogin: 0
    };
  },

  computed: { //  computed  store  index.js  state 
    ...mapState(['nowMenu'])
  },

  mounted() {
    // 
    if (sessionStorage.getItem("userInfo")) {
      this.isLogin = 1;
      this.getUserPlaylist();
    }
  },

  methods: { //  methods  store  index.js  mutations 
    ...mapMutations(['updateNowMenu']),

    chose(e) {
      //  0
      this.mineLoveAcive = 0; // console.log(e);
      // console.log(e.target.outerText);
      // 

      if (e.target.outerText == '') {
        this.$router.push('/DiscoverMusic');
      } else if (e.target.outerText == '') {
        this.$router.push('/Podcast');
      } else if (e.target.outerText == '') {
        this.$router.push('/Video');
      } else if (e.target.outerText == '') {
        this.$router.push('/Focus');
      } else if (e.target.outerText == '') {
        this.$router.push('/Live');
      } else if (e.target.outerText == 'FM') {
        this.$router.push('/Fm');
      } else if (e.target.outerText == '') {
        //  active 
        this.mineLoveAcive = 1; // 

        if (sessionStorage.getItem("userInfo")) this.goDetail(this.userLoveListId);
      } else if (e.target.outerText == '') {
        this.$router.push('/Lately');
      } else if (e.target.outerText == '') {
        this.$router.push('/Collect');
      } // 


      this.updateNowMenu(e.target.outerText); // console.log('' + this.nowMenu + ' ');
    },

    // ()()
    async getUserPlaylist() {
      this.user = JSON.parse(sessionStorage.getItem('userInfo')); // 

      const res = await getUserPlaylist(this.user.userid); // console.log('', res);

      this.playlist = res.data.playlist; // console.log('', this.playlist);
      // 

      let tempCreateLists = this.playlist.filter(item => item.creator.nickname == this.user.nickname); // id

      this.userLoveListId = tempCreateLists[0].id; // 

      tempCreateLists.splice(0, 1);
      this.userCreateLists = tempCreateLists; // console.log('', this.userCreateLists);

      this.userCollectLists = this.playlist.filter(item => item.creator.nickname !== this.user.nickname); // console.log('', this.userCollectLists);
    },

    // 
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1 2
          type: 1
        }
      });
    }

  }
};