import bus from './eventBus';
import { mapMutations, mapState } from "vuex";
import { getSongUrl } from '@/request/api/songInfo';
export default {
  data() {
    return {
      // 
      shadowDisplay: 0,
      // 
      playKeyDisplay: 1,
      // 
      icons: [{
        id: 1,
        name: '#icon-aixin',
        title: ''
      }, {
        id: 2,
        name: '#icon-favorite',
        title: ''
      }, {
        id: 3,
        name: '#icon-xiazai',
        title: ''
      }, {
        id: 4,
        name: '#icon-zhuanfa',
        title: ''
      }],
      // 
      isHideDetail: 0,
      // 
      interVal: 0,
      // 
      percentage: 0,
      // 
      isDoubleName: 0,
      // (/)
      isSongNameMoving: 0
    };
  },

  computed: { ...mapState(['playList', 'playListIndex', 'currentTime', 'duration'])
  },

  //  created 
  created() {
    //  SongDetailTopNav  hideDetail
    bus.on('hideDetail', val => {
      this.isHideDetail = val;
    }); // 

    window.addEventListener('keyup', this.space, true);
  },

  async mounted() {
    //  url
    const res = await getSongUrl(this.playList[this.playListIndex].id); // console.log(' url', res);
    //  store(vuex)dispatch()  store  actions 

    this.$store.dispatch('getLyrics', this.playList[this.playListIndex].id); //  audio 

    let _this = this;

    setTimeout(function () {
      _this.addDuration();
    }, 500); // console.log('', this.playList[this.playListIndex].name);
  },

  methods: {
    // 
    showDetail() {
      // 
      //   
      // this.$refs.playLeft.classList.value = this.$refs.playLeft.classList.value + ' leftDown'
      // 
      this.$refs.playLeft.classList = 'playLeft leftDown'; // 

      this.$emit('showDetail'); // 

      this.isHideDetail = 0;
    },

    // 
    hideDetail() {
      this.$refs.playLeft.classList = 'playLeft leftUp';
      this.$emit('hideDetail');
    },

    // 
    play() {
      this.playKeyDisplay = 0;
      bus.emit('isPlay', 1);
      this.$refs.audio.play(); // 

      this.updateTime();
    },

    // 
    pause() {
      this.playKeyDisplay = 1;
      bus.emit('isPlay', 0);
      this.$refs.audio.pause(); // 

      clearInterval(this.interVal);
    },

    // 
    space(e) {
      if (e.keyCode === 32) {
        if (this.playKeyDisplay == 1) {
          this.play();
        } else {
          this.pause();
        }
      }
    },

    // ( audio  timeupdate )
    updateTime() {
      this.interVal = setInterval(() => {
        // audio  currentTime 
        this.updateCurrentTime(this.$refs.audio.currentTime);
      }, 1000);
    },

    // 
    goPlay(num) {
      // 
      let index = this.playListIndex + num; // 

      if (index < 0) index = this.playList.length - 1; // 
      else if (index == this.playList.length) index = 0; // 

      this.updatePlayListIndex(index);
    },

    // 
    addDuration() {
      //  audio bug
      // console.log('audio ', this.$refs.audio.duration);
      // this.updateDuration(this.$refs.audio.duration)
      // 
      if (this.playList[this.playListIndex].dt) this.updateDuration(this.playList[this.playListIndex].dt / 1000);
      if (this.playList[this.playListIndex].duration) this.updateDuration(this.playList[this.playListIndex].duration / 1000);
    },

    // 
    fixDuration(duration) {
      let min = parseInt(duration / 60) < 10 ? '0' + parseInt(duration / 60) : parseInt(duration / 60);
      let sec = parseInt(duration % 60) < 10 ? '0' + parseInt(duration % 60) : parseInt(duration % 60);
      return min + ':' + sec;
    },

    //  VIP 
    isVipSong() {
      if (this.playList[this.playListIndex].fee == 1) {
        // fee: 0 /1 VIP 4 8 (fee  1  8  2 )
        // 
        this.pause();
        return true;
      } else {
        return false;
      }
    },

    // 
    moveSongName() {
      // /
      // if (this.$refs.songNameWordsSpan.offsetWidth > 150)
      if (this.isDoubleName && this.isSongNameMoving == 0) {
        // (/)
        this.isSongNameMoving = 1; // ()

        const moveTime = parseInt(this.$refs.songNameWordsSpan.offsetWidth * 0.024); //  transition

        this.$refs.songNameWordsDiv.style.transition = `all linear ${moveTime}s`; // 

        const moveWidth = this.$refs.songNameWordsSpan.offsetWidth + this.$refs.nbsps.offsetWidth;
        this.$refs.songNameWordsDiv.style.transform = `translateX(-${moveWidth}px)`; // console.log(this.$refs.songNameWordsDiv.style.transition, '', this.isSongNameMoving);
        // 

        setTimeout(() => {
          // (/)
          this.isSongNameMoving = 0; // 

          this.$refs.songNameWordsDiv.style.transition = this.$refs.songNameWordsDiv.style.transform = ''; // console.log(this.$refs.songNameWordsDiv.style.transition, '', this.isSongNameMoving);
        }, moveTime * 1000 + 500);
      }
    },

    ...mapMutations(['updateCurrentTime', 'updatePlayListIndex', 'updateDuration'])
  },
  watch: {
    // 
    isHideDetail() {
      if (this.isHideDetail == 1) this.hideDetail();
    },

    //  store ( store )
    playList() {
      // console.log('store !');
      //  VIP 
      if (!this.isVipSong()) {
        this.$refs.audio.autoplay = true; // 

        this.play();
      } // ( 0)


      if (this.playListIndex == 0) {
        // 
        this.isDoubleName = this.playList[this.playListIndex].name.length > 20 ? 1 : 0; // 

        this.$store.dispatch('getLyrics', this.playList[this.playListIndex].id); // 

        this.addDuration();
      }
    },

    //  store ()( store )
    "$store.state.playListIndex": {
      handler(newVal, oldVal) {
        // console.log(':', oldVal, ':', newVal);
        // 
        this.isDoubleName = this.playList[this.playListIndex].name.length > 20 ? 1 : 0; // 

        if (this.isSongNameMoving) {
          // (/)
          this.isSongNameMoving = 0; // 

          this.$refs.songNameWordsDiv.style.transition = this.$refs.songNameWordsDiv.style.transform = '';
        } //  VIP 


        if (!this.isVipSong()) {
          this.$refs.audio.autoplay = true; // 

          this.play(); // dispatch()  store  actions 

          this.$store.dispatch('getLyrics', this.playList[this.playListIndex].id); // 

          this.addDuration();
        }
      }

    },

    //  newValue  currentTime 
    currentTime(newValue) {
      this.percentage = this.currentTime / this.duration * 100 <= 100 ? this.currentTime / this.duration * 100 : 100; // console.log('' + this.percentage);
      // 

      if (parseInt(newValue) == parseInt(this.duration)) {
        // 
        if (this.playListIndex === this.playList.length - 1) {
          this.updatePlayListIndex(0);
        } else {
          this.goPlay(1);
        }
      }
    }

  }
};