//  lodash 
import debounce from 'lodash/debounce'; //  eventBus 

import bus from '@/components/home/eventBus.js'; //  store  mapState  

import store from '@/store/index.js'; // 

import { mapMutations, mapState } from 'vuex';
import { getHotSearch, getDefaultSearch, getSearch, getSearchSuggest } from '@/request/api/home.js';
import { getUserLogout, getUserDetail, getUserAccount, getUserSubcount, getUserLevel, getUserBinding, DailySignin } from '@/request/api/loginBox.js';
export default {
  data() {
    return {
      // (LoginBox)
      isLogin: 0,
      // 
      isSearchBox: false,
      // 
      isAccountBox: false,
      // 
      searchSuggest: {},
      // 
      searchList: [],
      // 
      hotSearch: [],
      // 
      defaultSearch: '',
      // 
      searchKey: '',
      // 
      keyWordList: [],
      // ""
      lookAll: '',
      // 
      user: {
        // id
        userid: 0,
        // 
        nickname: '',
        // 
        avatarUrl: '',
        // 
        follows: 0,
        // 
        followeds: 0,
        // 
        locationStatus: 0,
        // 
        signature: '',
        // 
        level: 0,
        // 
        listenSongs: 0,
        // 
        gender: 1,
        // 
        newUser: false,
        // 
        province: 0,
        // 
        city: 0,
        // ()
        birthday: 0,
        // 
        createTime: 0,
        // 
        playlistCount: 0,
        // 
        vipType: 0
      },
      //  
      hasUser: 0,
      // 
      isLeft: 0,
      // 
      isRight: 0
    };
  },

  // 
  async mounted() {
    // 
    // if (sessionStorage.getItem('user') != null) this.isLogin = sessionStorage.getItem('user')
    // if (sessionStorage.getItem('user') == null) this.isLogin = false
    this.isLogin = sessionStorage.getItem('user') == null ? false : sessionStorage.getItem('user'); //  localStorage  keyWordList 

    this.keyWordList = JSON.parse(localStorage.getItem('keyWordList')) ? JSON.parse(localStorage.getItem('keyWordList')) : []; // console.log(this.keyWordList);
    // 

    let res = await getHotSearch(); // console.log('', res);

    this.hotSearch = res.data.data; // console.log('', this.hotSearch);
    // 

    let res1 = await getDefaultSearch();
    this.defaultSearch = res1.data.data.showKeyword; // console.log('', this.defaultSearch);
    //  localStorage  keyWordList 

    this.keyWordList = JSON.parse(localStorage.getItem('keyWordList')) ? JSON.parse(localStorage.getItem('keyWordList')) : []; // 

    window.addEventListener('click', this.otherClose); //  id

    if (sessionStorage.getItem("userInfo")) {
      //  Id  LoginBox 
      // JSON.parse() 
      this.user = JSON.parse(sessionStorage.getItem('userInfo')); // ()

      if (this.user.userid !== 0) this.getUserInfo(this.user.userid); // 
      // try {
      //   let res2 = await DailySignin(sessionStorage.getItem("cookie"))
      //   // console.log('', res2);
      // } catch(e) {
      //   console.log(e.name, '');
      // }
    }
  },

  beforeDestroy() {
    /*  */
    window.removeEventListener('click', this.otherClose);
  },

  methods: {
    // input (debounce )
    input: debounce(function (keywords) {
      // 
      this.searchKey = keywords.trim(); // 

      if (this.searchKey !== '') this.getSearchSuggest();
    }, 1000),

    // 
    async getSearchSuggest() {
      const res = await getSearchSuggest(this.searchKey); // console.log('', res);

      this.searchSuggest = res.data.result; // console.log('', this.searchSuggest);
    },

    // ()
    async getUserInfo(uid) {
      // 
      let res = await getUserDetail(uid, sessionStorage.getItem("cookie")); // console.log('getUserInfo', res);

      this.user.follows = res.data.profile.follows;
      this.user.followeds = res.data.profile.followeds;
      this.user.signature = res.data.profile.signature;
      this.user.level = res.data.level;
      this.user.listenSongs = res.data.listenSongs;
      this.user.gender = res.data.profile.gender;
      this.user.newUser = res.data.newUser;
      this.user.province = res.data.profile.province;
      this.user.city = res.data.profile.city;
      this.user.birthday = this.timestampToTime(res.data.profile.birthday);
      this.user.createTime = this.timestampToTime(res.data.profile.createTime);
      this.user.playlistCount = res.data.profile.playlistCount;
      this.user.vipType = res.data.profile.vipType; // console.log('', this.user);
      //  LoginBox.vue  userInfo

      sessionStorage.setItem('userInfo', JSON.stringify(this.user)); //  vuex
      // ! bug vuex 

      store.state.user = this.user; // console.log('store  user', store.state.user);
    },

    //  CSDNhttps://blog.csdn.net/hong521520/article/details/105855514
    timestampToTime(timestamp) {
      // 10 *100013 1000
      var date = new Date(timestamp);
      var Y = date.getFullYear() + '-';
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      return Y + M + D;
    },

    // 
    async searchHistory(item) {
      //  || 
      if (store.state.searchKey !== item || this.$route.path !== '/Search/SearchSingle') {
        //  Vuex
        store.state.searchKey = item; // 

        this.searchKey = item; //  searchKey  Search  SearchSingle 

        bus.emit('searchKey', item); // 

        sessionStorage.setItem("searchKey", item); //  keyWordList ()
        // unshift() 

        this.keyWordList.unshift(item); // ...new Set()   Set 

        this.keyWordList = [...new Set(this.keyWordList)]; //  splice(index, howmany) >  index howmany 

        if (this.keyWordList.length > 10) this.keyWordList.splice(this.keyWordList.length - 1, 1); //  localStorage keyWordList  JSON 

        localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // 
        // let res = await getSearch(item)
        // console.log('', res);
        // (this.$route.path)

        if (this.$route.path !== '/Search/SearchSingle') this.$router.push('/Search');
      } else {
        //  searchKey  Search  SearchSingle 
        bus.emit('searchKey', item);
      } // 


      this.isSearchBox = false;
    },

    // 
    searchHot(item) {
      // 
      this.isSearchBox = false; //  Vuex

      store.state.searchKey = item; // 

      this.searchKey = item; //  searchKey  Search  SearchSingle 

      bus.emit('searchKey', item); // searchKey 

      sessionStorage.setItem('searchKey', item); // unshift() 

      this.keyWordList.unshift(this.searchKey); //   Set 

      this.keyWordList = [...new Set(this.keyWordList)]; // 

      if (this.keyWordList.length > 10) this.keyWordList.splice(this.keyWordList.length - 1, 1); // keyWordList 

      localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // (this.$route.path)

      if (this.$route.path !== '/Search/SearchSingle') this.$router.push('/Search');
    },

    // 
    async enterKey() {
      // 
      this.searchKey = this.searchKey.trim();

      if (this.searchKey !== '') {
        //  searchKey  Search  SearchSingle 
        bus.emit('searchKey', this.searchKey); // console.log('' + this.searchKey + '...');
        // unshift() 

        this.keyWordList.unshift(this.searchKey); //   Set 
        // console.log(...new Set(this.keyWordList));

        this.keyWordList = [...new Set(this.keyWordList)]; // 

        if (this.keyWordList.length > 10) {
          //  splice(index, howmany) > indexhowmany
          this.keyWordList.splice(this.keyWordList.length - 1, 1);
        } //  localStorage keyWordList  JSON 


        localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); //  Vuex

        store.state.searchKey = this.searchKey; // 
        // let res = await getSearch(this.searchKey)
        // console.log(res);
        // this.searchList = res.data.result.songs
        // console.log(this.searchList);
        // `
        // this.searchKey = ''
        //  SearchBox 

        this.isSearchBox = false; // 

        this.$router.push('/Search');
      }
    },

    // 
    delHistory() {
      var bool = confirm("");

      if (bool == true) {
        localStorage.removeItem('keyWordList');
        this.keyWordList = [];
      } else {
        return;
      }
    },

    //  searchBox
    changeIsSearchBox() {
      this.isSearchBox = !this.isSearchBox;
    },

    // 
    spanKeyMouseover(index) {
      this.$refs.delete[index].style.display = 'inline-block';
    },

    // 
    spanMouseleave(index) {
      this.$refs.delete[index].style.display = 'none';
    },

    // 
    allKeyWordList() {
      this.$refs.keyWordList.style.height = 'auto';
      this.lookAll = '';
    },

    // 
    deleteKeyWord(index) {
      // console.log(index, this.keyWordList[index]);
      // splice(index, n) indexn()
      this.keyWordList.splice(index, 1);
      localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList)); // console.log(this.keyWordList);
      // this.isSearchBox = true
      // '' 
      // this.lookAll = ''
      // this.$refs.keyWordList.style.height = '76px'
    },

    // 
    refresh() {
      // 
      // this.$router.go(0);
      //  location ( true )
      location.reload(true); // console.log('location ', location);
    },

    // 
    showLoginBox() {
      this.updateIsLoginBox(true);
    },

    // 
    async showAccountBox() {
      this.isAccountBox = !this.isAccountBox;
    },

    //  input 
    focus() {
      this.isSearchBox = true; // console.log(' isSearchBox' + this.isSearchBox);
    },

    // input 
    blur() {// this.isSearchBox = false
      // console.log(' isSearchBox' + this.isSearchBox);
    },

    // 
    previous() {
      // this.$router.go(-1)
      this.$router.back(); // console.log(this.$route.path);
      // console.log(this.$router);
    },

    // 
    next() {
      this.$router.go(1);
    },

    // 
    otherClose(e) {
      //  searchBox.contains()  this.isSearchBox == true 
      if (this.isSearchBox == true && this.$refs.searchBox.contains(e.target)) {// console.log('');
      }

      if (this.isSearchBox == true && !this.$refs.searchBox.contains(e.target) && e.target != this.$refs.inputref) {
        // console.log('!');
        this.isSearchBox = false;
      }

      if (this.isAccountBox == true && this.$refs.accountBox && this.$refs.accountBox.contains(e.target)) {// console.log('');
      }

      if (this.isAccountBox == true && this.$refs.accountBox && !this.$refs.accountBox.contains(e.target) && e.target != this.$refs.accountBoxChange1 && e.target != this.$refs.accountBoxChange2) {
        // console.log('!');
        this.isAccountBox = false;
      }
    },

    // 
    async logout() {
      let res = await getUserLogout(); // console.log(res);
      //  sesstion 

      sessionStorage.clear(); //  sesstion 
      // sessionStorage.removeItem("a");
      // /

      this.$router.go(0);
    },

    // 
    gotoPersonalHome() {
      if (this.user.userid !== 0) {
        this.$router.push({
          path: '/PersonalHome',
          query: {
            userId: this.user.userid
          }
        });
      }
    },

    // /
    goFollows(id, type, nickname, limit) {
