      this.isAccountBox = 0;
      this.$router.push({
        path: '/Follows',
        query: {
          id: id,
          // 1 2
          type: type,
          nickname: nickname,
          limit: limit
        }
      });
    },

    // 
    goTrends(id, nickname, limit) {
      // 
      this.isAccountBox = 0;
