    // 
    goSingerPage(id) {
      this.isSearchBox = false;
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 
    goPlayListDetail(id) {
      //  SearchBox 
      this.isSearchBox = false; // 

      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1 2
          type: 1
        }
      });
    },

    // 
    goAlbumDetail(id) {
      this.isSearchBox = false;
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          type: 2
        }
      });
    },

    //  methods  store  index.js  mutations 
    ...mapMutations(['updateIsLoginBox'])
  },
  watch: {
    isSearchBox() {
      //  '' 
      // console.log('isSearchBox ');
      this.lookAll = '';
      this.$refs.keyWordList.style.height = '76px';
    },

    // 
    '$route': function (newRoute, oldRoute) {
      // console.log('newRoute:', newRoute, 'oldRoute:', oldRoute);
      // console.log('window.history:', window.history);
      // console.log('window.history.state:', window.history.state);
      // 
      // this.isLeft = (typeof window.history.state.back !== 'undefined') ? 1 : 0
      this.isLeft = window.history.state.back ? 1 : 0;
      this.isRight = window.history.state.forward ? 1 : 0; // console.log(this.isLeft, this.isRight);
