//  app.config.globalProperties 
export default function MyPlugin(app) {
  //  app.config.globalProperties /
  //  0315
  app.config.globalProperties.$formatIndex = function (index) {
    return index >= 10 ? index : (index = "0" + index);
  };
  // () 71651
  app.config.globalProperties.$formatCount = function (num) {
    if (num >= 100000000) {
      //  0
      return (num / 100000000).toFixed(0) + "";
    } else if (num >= 100000) {
      return (num / 10000).toFixed(0) + "";
    } else return num
  };
  // () 03:37
  app.config.globalProperties.$formatDuration = function (time) {
    let dt = time / 1000;
    let m = parseInt(dt / 60);
    let s = parseInt(dt % 60); // Math.ceil
    m >= 10 ? m : (m = "0" + m);
    s >= 10 ? s : (s = "0" + s);
    return m + ":" + s;
  };
  //  2023-01-01
  app.config.globalProperties.$formatDate = function (time) {
    // 
    var date = new Date(time)
    var Y = date.getFullYear()
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1)
    var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
    return Y + '-' + M + '-' + D
  };
};