//  eventBus 
import bus from '@/components/menu/DiscoverMusic/DiscoverRecommend/eventBus'; // 

import { mapMutations, mapState } from 'vuex';
import { getMusicComment } from '@/request/api/songInfo';
export default {
  data() {
    return {
      // 
      isLoading: 1,
      //  id 
      // songId: 0,
      // 
      totalNum: 0,
      // 
      hotComments: [],
      // 
      tenHotComments: [],
      // 
      latestComments: [],
      // 
      isTab: 0
    };
  },

  // 
  props: {
    // 
    isFirstShow: 0
  },

  mounted() {// console.log(' isFirstShow', this.isFirstShow);
  },

  methods: {
    // 
    async getComments(id) {
      // 
      this.isLoading = 1;
      const res = await getMusicComment(id, 20, sessionStorage.getItem("cookie")); // console.log('', res);

      this.totalNum = res.data.total;
      this.hotComments = res.data.hotComments; // isTab  tenHotComments 

      this.isTab = this.hotComments.length > 10 ? 1 : 0;
      this.tenHotComments = this.hotComments.length > 10 ? this.hotComments.splice(0, 10) : this.hotComments;
      this.latestComments = res.data.comments; // console.log('', this.tenHotComments);
      // console.log('', this.latestComments);
      // 

      this.isLoading = 0;
    },

    // 
    publishTime(time, timeStr) {
      // 
      // test()  true  false
      const reg = //; // if (timeStr.indexOf('') != -1)

      if (reg.test(timeStr)) {
        // console.log('', timeStr);
        // '' slice() 
        let newStr = timeStr.slice(0, 2) + ' ' + timeStr.slice(2, timeStr.length); // console.log(newStr);

        return newStr;
      } else {
        // 
        let YNow = new Date().getFullYear(); // 

        let date = new Date(time);
        let Y = date.getFullYear();
        let M = date.getMonth() + 1;
        let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        let H = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        let Min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes(); // 

        if (Y === YNow) return M + '' + D + ' ' + H + Min;else return Y + '' + M + '' + D + ' ' + H + Min;
      }
    },

    // ( id)
    goMoreHotComments() {
      //  SongDetailTopNav.vue  goDown
      this.$emit('goDown1'); // 

      this.$router.push({
        path: '/HotComments',
        query: {
          id: this.playList[this.playListIndex].id,
          //  1 2 3 
          type: 3
        }
      }); // bug
      // bug store 
      // let _this = this
      // setTimeout(function () {
      //   _this.$router.go(0)
      // }, 500);
    },

    // 
    mouseenter(index, type) {
      // console.log(index, type);
      if (type === 1) this.$refs.report[index].style.display = 'block';
      if (type === 2) this.$refs.latestReport[index].style.display = 'block';
    },

    // 
    mouseleave(type) {
      if (type === 1) {
        for (let i = 0; i < this.$refs.report.length; i++) {
          this.$refs.report[i].style.display = 'none';
        }
      } else {
        for (let i = 0; i < this.$refs.latestReport.length; i++) {
          this.$refs.latestReport[i].style.display = 'none';
        }
      }
    },

    // 
    zanMouseenter(index, type) {
      if (type === 1) {
        // this.$refs.zanIcon  this.$refs.zanNum 
        for (let i = 0; i < this.$refs.zanIcon.length; i++) {
          this.$refs.zanIcon[i].style.fill = '#545454';
          this.$refs.zanNum[i].style.color = '#a7a5a5';
        }

        this.$refs.zanIcon[index].style.fill = '#000';
        this.$refs.zanNum[index].style.color = '#000';
      } else {
        for (let i = 0; i < this.$refs.latestZanIcon.length; i++) {
          this.$refs.latestZanIcon[i].style.fill = '#545454';
          this.$refs.latestZanNum[i].style.color = '#a7a5a5';
        }

        this.$refs.latestZanIcon[index].style.fill = '#000';
        this.$refs.latestZanNum[index].style.color = '#000';
      }
    },

    // 
    zanMouseleave(type) {
      if (type == 1) {
        for (let i = 0; i < this.$refs.zanIcon.length; i++) {
          this.$refs.zanIcon[i].style.fill = '#545454';
          this.$refs.zanNum[i].style.color = '#a7a5a5';
        }
      } else {
        for (let i = 0; i < this.$refs.latestZanIcon.length; i++) {
          this.$refs.latestZanIcon[i].style.fill = '#545454';
          this.$refs.latestZanNum[i].style.color = '#a7a5a5';
        }
      }
    },

    ...mapMutations(["updateHotCommentsType"])
  },
  watch: {
    // ()
    isFirstShow() {
      // 
      if (sessionStorage.getItem("cookie") && this.isFirstShow == 1) this.getComments(this.playList[this.playListIndex].id);
    },

    //  store ()
    playListIndex() {
      this.getComments(this.playList[this.playListIndex].id);
    }

  },
  computed: { ...mapState(['playList', 'playListIndex'])
  }
};